.about {
  background: linear-gradient(to top, #fff 0%,#ddd 100%);

  &__wrap {
    padding: 35px 0;
    display: flex;
    flex-direction: column; }

  &__title {
    text-align: center;
    padding: 60px 0 30px;
    margin: 0 auto;
    max-width: 920px;
    border-bottom: 1px solid #333;

    @media (max-width: 960px) {
      margin: 0 10px; } }

  &__fig-text-wrap {
    display: flex;
    flex-direction: row;
    margin-bottom: 60px;

    @media (max-width: 960px) {
      display: block;
      margin-bottom: 0px; } }

  //  =========== About =========== Figure Area  ===========
  &__figure {
    overflow: hidden;
    position: relative;
    box-shadow: $box-shadow;
    transition: $basic-trans;
    display: flex;
    justify-content: center;
    align-items: center;

    &:hover {
      box-shadow: $box-shadow--hover; }

    // show figure caption after hover
    &:hover .about__fig-caption {
      opacity: 1;
      pointer-events: none;
      bottom: 15px; } }

  &__figure.col-2 {
    margin-right: 0;

    @media (max-width: 960px) {
      max-width: 100%;
      margin-right: 10px;
      margin-bottom: 10px; } }

  &__fig-img {
    transform: scale(1.02);
    transition: $basic-trans;

    &:hover {
      transform: scale(1.33) rotate(20deg);
      filter: grayscale(.7);

      @media (max-width: 960px) {
        transform: scale(1.02);
        filter: none; } } }


  &__fig-caption {
    position: absolute;
    bottom: -1%;
    left: 15px;
    right: 15px;
    font-size: 2rem;
    line-height: 1.1;
    color: #eee;
    background: $transparent-back;
    padding: 15px;
    text-align: center;
    opacity: 0;
    transition: $basic-trans;

    @media (max-width: 960px) {
      bottom: 15px;
      opacity: 1; } }


  //  =========== About =========== Text Area  ===========
  &__text-wrap {
    position: relative; }

  &__text-wrap.col-1 {
    @media (max-width: 960px) {
      max-width: 100%;
      margin-top: 30px; } }

  &__text-title {
    margin-top: -7px;

    @media (max-width: 960px) {
      margin-top: 30px;
      text-align: center; } }

  &__text-desc {
    @media (max-width: 960px) {
      padding-bottom: 30px; } }

  .cta {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    margin: 0;

    @media (max-width: 960px) {
      position: static;
      max-width: 100%; } } }

