html {
  box-sizing: border-box;
  position: relative; }

*, *::after, *::before {
  box-sizing: inherit; }

.wrap--global {
  width: 800px;
  margin-left: auto;
  margin-right: auto; }

.main-wrapper {
  margin-bottom: 400px;

  @media (max-width: 960px) {
    margin-bottom: 0; }
  //
  // @media (max-width: 620px)
 }  //   margin-bottom: 700px

.element-invisible {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px); /* IE6, IE7 */;
  clip: rect(1px, 1px, 1px, 1px); }

//Flex Clases
.flex {
  display: flex;
  flex-direction: column; }

.flex--center {
  justify-content: center; }

.flex--space-around {
  justify-content: space-around; }

.flex--space-between {
  justify-content: space-between; }

.order-first {
  order: -1; }


//columns
.col-1 {
  max-width: 300px;
  margin: 0 10px;
  flex: 1;

  @media (max-width: 620px) {
    max-width: 100%; } }

.col-2 {
  max-width: 620px;
  margin: 0 10px;
  // width: 100%
  flex: 1; }

.col-3 {
  max-width: 940px;
  margin: 0 auto;
 }  // width: 100%

.col-50 {
  width: 50%; }

.push-right {
  margin-left: 330px; }
