.parallax {
  overflow: hidden;
  position: relative;
  box-shadow: $box-shadow;

  &__wrap {
    height: 300px;
    background: {
      image: url("../img/parallax-bg.jpg");
      attachment: fixed;
      size: cover;
      position: center; }

    transform: scale(1.3);

    @media (max-width: 960px) {
      background-attachment: local;
      transform: none;
      height: 300px; } }

  &__text-wrap {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transform: scale(.8);
    text-align: center; }


  &__title {
    color: #eee;
    text-shadow: 0px 4px 8px rgba(0,0,0,.7);
    font-size: 4rem;
    margin-bottom: 1rem; }

  &__content {
    color: #eee;
    font-size: 1.2rem;
    text-shadow: 0px 2px 2px rgba(0,0,0,.7);
    background: rgba(0,0,0,.4);
    padding: .5rem;
    border-radius: 2px;
    box-shadow: 0px 0px 3px 3px rgba(0,0,0,.4); }

  &--desc {
    @media (max-width: 960px) {
      display: none; } }

  &--mob {
    @media (min-width: 961px) {
      display: none; } } }
