.footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  background: #222;
  z-index: -1;

  @media (max-width: 960px) {
    position: static; }

  &__wrapper {
    display: flex;
    justify-content: center;
    padding: 60px 0;

    @media (max-width: 960px) {
      padding: 50px 0 20px; }

    @media (max-width: 620px) {
      display: block;
      text-align: center; } }


  //  =========== Footer =========== Icon  ===========
  &__icon {
    // text-align: center
    display: flex;
    justify-content: center;
    align-items: center;

    @media (max-width: 960px) {
      display: none; } }

  &__img {
    height: 85%;
    width: 85%; }


  //  =========== Footer =========== Links  ===========
  &__links-title,
  &__form-title {
    color: #eee;
    margin-top: 0;
    font-size: 2rem;

    @media (max-width: 960px) {
      text-align: center; } }

  &__links-list {
    list-style: none;
    margin: 0;
    padding: 0;

    @media (max-width: 960px) {
      text-align: center; }

    @media (max-width: 620px) {
      margin-bottom: 40px; } }

  &__links-list li:first-child {
    border-top: 1px solid white; }

  &__links-link {
    color: #eee;
    display: block;
    text-transform: uppercase;
    font-size: 1.4rem;
    padding: 5px 0;
    border-bottom: 1px solid transparent;
    border-top: 1px solid transparent;

    &:hover, :active {
      color: #fff;
      text-shadow: 0px 2px 6px rgba(255,255,255,.3);
      border-bottom: 1px solid white; } }

  //  =========== Footer =========== Links  ===========
  // &__form-title
  //   color: #eee
  //   margin-top: 0

  &__form-item {
    width: 100%;
    padding: 5px 0;

    @media (max-width: 960px) {
      text-align: center; } }

  &__form-btn {
    padding: 5px;
    background: #fff;
    width: 50%;
    border-radius: 0; } }
